// import '@babel/polyfill'
import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'


// fontawsome
import {library} from '@fortawesome/fontawesome-svg-core'
import {faUser as fasUser,
        faHeart as fasHeart,
        faShoppingCart as fasShoppingCart,
        faShareAlt as fasShareAlt,
        faSearch as fasSearch,
        faTimes as fasTimes,
        faArrowUp as fasArrowUp,
        faChevronLeft as fasChevronLeft,
        faChevronRight as fasChevronRight,
        faSortUp as fasSortUp,
        faSortDown as fasSortDown,
      } from '@fortawesome/free-solid-svg-icons'
import { 
      faUser as farUser,
      faHeart as farHeart,
      faClosedCaptioning as farClosedCaptioning
    } from '@fortawesome/free-regular-svg-icons'

import {faInstagram as fabInstagram,
        faTwitter as fabTwitter,
        faFacebookSquare as fabFacebookSquare,
        faCreativeCommonsBy as fabCreativeCommonsBy,
        faCreativeCommonsNc as fabCreativeCommonsNc,
        faCreativeCommonsNd as fabCreativeCommonsNd,
        faCreativeCommonsSa as fabCreativeCommonsSa
  } from '@fortawesome/free-brands-svg-icons'

import {FontAwesomeIcon} from '@fortawesome/vue-fontawesome'
library.add(
    // 검색 아이콘
    fasUser, farUser,
            fasHeart, farHeart,
            fasShoppingCart,
            fabInstagram,
            fabTwitter,
            fabFacebookSquare,
            fasShareAlt,
            fasSearch,
            fasTimes,
            fasArrowUp,
            fasSortUp,
            fasSortDown,
            fasChevronLeft,
            fasChevronRight,
            farClosedCaptioning,
            fabCreativeCommonsBy,
            fabCreativeCommonsNc,
            fabCreativeCommonsNd,
            fabCreativeCommonsSa
    )
    

const app = createApp(App)



app.use(store)
app.use(router)
app.config.globalProperties.axios=axios


// 자동 로그인
const userString = localStorage.getItem('user')
    if(userString){
      const userData = JSON.parse(userString)
      store.commit('SET_USER_DATA',userData)
    }


// 카트 새로고침 유지
// const cart = localStorage.getItem('cart')
//     if(cart){
//       const items = JSON.parse(cart)
//       store.commit('SET_CART_DATA', items)
//     }

app.component('FontAwesomeIcon', FontAwesomeIcon)
app.mount('#app')