<template>

  <div id="app">
    <div id="user-box">
      <Header v-if="$route.meta.state !== 'none'"></Header>
      <div class="section-box" v-if="$route.meta.state == 'none'">
        <Nav
          @sbagdata="sbagTF"
          @Accdata="accTF"
          @Contsdata="contsTF"
          @divdata="divTF"
          @ntcboard="ntcboardTF"
          @faqboard="faqboardTF"
          @questboard="questboardTF"
          @reviewboard="reviewboardTF"
          v-if="$route.meta.state == 'none'"
        ></Nav>
        <section>
          <article>
            <router-view
            @sbagdata="sbagTF"
            @Accdata="accTF"
            @Contsdata="contsTF"
            @divdata="divTF"
            @ntcboard="ntcboardTF"
              :sbag="sbag"
              :acc="acc"
              :conts="conts"
              :device="device"
              :notice="notice"
              :faq="faq"
              :quest="quest"
              :review="review"
            ></router-view>
          </article>
        </section>
      </div>
      <div v-if="$route.meta.state !== 'none'">
        <section>
          <article>
            <router-view></router-view>
          </article>
        </section>
      </div>
      <Footer v-if="$route.meta.state !== 'none'"></Footer>
    </div>
  </div>
</template>

<script>

import Header from "@/components/Common/Header.vue";
import Footer from "@/components/Common/Footer.vue";
import Nav from "@/components/admin/AdminNav.vue";


// const params = new URLSearchParams();

import {mapGetters} from 'vuex'

export default {
  name: 'App',
  data(){
    return{
      user: [],
      preUrl: '',
      pageIndex: 0,
      isLoad: true,
      // likes: false,



      // 관리자 모드 확인
      admin : false,

      // 관리자 제품 관리
      sbag : false,
      conts: false,
      acc: false,
      device: false,

      // 관리자 게시물 관리
      notice: false,
      faq: false,
      quest: false,
      review: false,


      // 헤더, 푸터 조건식
      visible: true,

     

    }
  },
  watch:{
    loggedIn(){
      if(this.loggedIn){
        this.user = this.$store.state.user
        // console.log(typeof this.$store.state.user);
      }else{
        this.user = []
      }
    },
  },
  
  mounted(){
    setTimeout(() => {
      if(this.isLoad == true){
        this.isLoad = false
      }
    }, 5000);
    this.login()
  },
  
  computed:{
    ...mapGetters(['loggedIn']),
  },
  methods:{
    // intro(){
    //   var time 
    //   time = setInterval(() => {

    //   })
    // },
    // },

    login(){
      if(this.loggedIn){
      this.user = this.$store.state.user
        // console.log(this.$store.state.user.id);
      }else{
        this.user = []
      }
    },
    logOutEv(){
      this.$store.dispatch('logout')
      .then(() => {
        this.$router.push({path:'/'})
        this.user = ''
      }).catch((err) => {
        console.log(err);
      });
    },

    // 화면 리로드
    gotoUrl(toUrl){
      
      if (this.preUrl === this.$route.path) {
          this.$router.push(toUrl + '?' + this.pageIndex++)
      }
      this.preUrl = this.$route.path
    },


    intoadmin(){

      // if(!admin == this.$router.name == "Admin"){
      //   this.admin = true
      // }else{
      //   this.admin = false
      // }

      if(this.$router.name == "Admin"){
        this.admin = true
      }else{
        this.admin = false
      }
    },



    // 관리자 페이지
    sbagTF(){
      this.sbag = true;
      this.acc = false;
      this.conts = false;
      this.device = false;

      this.$router.push({ name: 'Adminitem' , path:'/admin/item'})
    },
    accTF(){
      this.sbag = false;
      this.acc = true;
      this.conts = false;
      this.device = false;

      this.$router.push({ name: 'Adminitem' , path:'/admin/item'})
    },
    contsTF(){
      this.sbag = false;
      this.acc = false;
      this.conts = true;
      this.device = false;

      this.$router.push({ name: 'Adminitem' , path:'/admin/item'})
    },
    divTF(){
      this.sbag = false;
      this.acc = false;
      this.conts = false;
      this.device = true;

      this.$router.push({ name: 'Adminitem' , path:'/admin/item'})
    },


// 관리자 게시물 관리
    ntcboardTF(){
      this.notice = true;
      this.faq = false;
      this.quest = false;
      this.review = false;

      this.$router.push({ name:'AdminNtcBoard', path:'/admin/ntcboard'})
    },
    faqboardTF(){
      this.notice = false;
      this.faq = true;
      this.quest = false;
      this.review = false
    },
    questboardTF(){
      this.notice = false;
      this.faq = false;
      this.quest = true;
      this.review = false;
    },
    reviewboardTF(){
      this.notice = false;
      this.faq = false;
      this.quest = false;
      this.review = true;
    }

  },

  components: {
    Header,
    Footer,
    Nav
  }
}
</script>

<style lang="scss">

@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
@import '@/assets/css/common/modal.scss';
@import '@/assets/css/common/common.scss';



</style>
