import { createStore } from 'vuex'
import axios from 'axios'


// const local = 'http://14.63.31.155:8088'
// const server = 'http://ec2-3-36-180-201.ap-northeast-2.compute.amazonaws.com:8088'

export default createStore({
  state: {
    user: null,
    cart: [],
  },
  getters:{
    loggedIn(state) {
        return !!state.user
    },

    cartdata(state){
      return state.cart
    }
  },
  mutations: {
    SET_USER_DATA(state, USER) {
        state.user = USER
        localStorage.setItem('user', JSON.stringify(USER))
      return axios.defaults.headers.common['Authorization'] = `Bearer${USER}`
    },

    LOGOUT(state){
        state.user = null
        localStorage.removeItem('user')
        return axios.defaults.headers.common['Authoriztion'] = null
    },

    SET_CART_DATA(state, CART){

      if(state.user != null){
        state.cart.push(
          ...CART
        );
  
        localStorage.setItem('cart', JSON.stringify(state.cart))
      }
      // localStorage.cart.clear()
      
    },
    DEL_CART_DATA(state, id){

      // console.log(state.cart);
      // console.log(id);
        for (let i = 0; i < id.length; i++) {
          state.cart = state.cart.filter(CART => CART.id !== id[i].id)  
        }
        console.log(state.cart);
        // localStorage.clear('cart')
    }
  },
  actions: {

    logout({commit}){
        commit('LOGOUT')
    },

  },
  modules: {
  }
})
